<template>
	<div class="regist">
		<el-form class="form" :model="registFrom" status-icon :rules="rules" ref="registFrom">
			<el-form-item prop="userName">
				<el-input type="text" v-model="registFrom.userName" autocomplete="off" placeholder="用户名">
					<template slot="prepend"><i class="el-icon-user"></i></template>
				</el-input>
			</el-form-item>
			<el-form-item prop="password">
				<el-input type="password" v-model="registFrom.password" autocomplete="off" placeholder="密码">
					<template slot="prepend"><i class="el-icon-key"></i></template>
				</el-input>
			</el-form-item>
			<el-form-item prop="repassword">
				<el-input type="password" v-model="registFrom.repassword" autocomplete="off" placeholder="确认密码">
					<template slot="prepend"><i class="el-icon-key"></i></template>
				</el-input>
			</el-form-item>
			<el-form-item>
				<el-button type="primary" @click="submitForm('registFrom')">注册</el-button>
				<el-button @click="resetForm('registFrom')">重置</el-button>
			</el-form-item>
		</el-form>
	</div>
</template>

<script>
import { registApi } from '@/api/user';
export default {
	name: 'regist',
	data() {
		// 用户名的验证函数
		// value 是输入框的值
		var validateUserName = (rule, value, callback) => {
			if (value === '') {
				callback(new Error('请输入用户名'));
			} else {
				if (/^[\w\u4e00-\u9fa5]{2,12}$/.test(value)) {
					callback();
				} else {
					callback(new Error('只能包含2-12位字母、数字和汉字'));
				}
			}
		};
		var validatePassword = (rule, value, callback) => {
			if (value === '') {
				callback(new Error('请输入密码'));
			} else {
				if (/^\w{6,18}$/.test(value)) {
					callback();
				} else {
					callback(new Error('只能包含数字、字母、下划线6-18位'));
				}
			}
		};
		var validateRepassword = (rule, value, callback) => {
			if (value === '') {
				callback(new Error('请再次输入密码'));
			} else if (value !== this.registFrom.password) {
				callback(new Error('两次输入密码不一致!'));
			} else {
				callback();
			}
		};
		return {
			registFrom: {
				userName: '',
				password: '',
				repassword: '',
			},
			// 定义每一项的验证
			rules: {
				userName: [{ validator: validateUserName, trigger: 'blur' }], //用户名的验证
				password: [{ validator: validatePassword, trigger: 'blur' }], //密码的验证
				repassword: [{ validator: validateRepassword, trigger: 'blur' }],
			},
		};
	},
	deactivated() {
		// this.resetForm('registFrom');
		this.$refs['registFrom'].resetFields();
	},
	methods: {
		submitForm(formName) {
			// 提交的验证函数：整体验证，当表单全部通过之后 valid为true，表单有一项没有通过 valid为false
			this.$refs[formName].validate(async valid => {
				if (valid) {
					let res = await registApi(this.registFrom);
					if (res.code == 1) {
						this.$message.success(res.data);
						this.$router.replace('/login');
					} else {
						this.$message.warning(res.data);
					}
				} else {
					this.$message.warning('账号或者密码不符合要求');
					return false;
				}
			});
		},
		resetForm(formName) {
			this.$refs[formName].resetFields();
		},
	},
};
</script>

<style scoped>
.regist {
	margin-top: 100px;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}
.regist >>> .form {
	width: 30%;
}
</style>
